import { createAsyncThunk, createSlice, isAnyOf } from "@reduxjs/toolkit";
import { User } from "../../app/model/user";
import { FieldValues } from "react-hook-form";
import service from "../../app/api/service";
import { history } from "../..";
import { toast } from "react-toastify";
import { setBasket } from "../basket/basketSlice";

interface AccountState {
	user: User | null;
}

const initialState: AccountState = {
	user: null,
};

export const signInUser = createAsyncThunk<User, FieldValues>("account/signInUser", async (data, thunkAPI) => {
	try {
		const userDto = await service.Account.login(data);
		const { basket, ...user } = userDto;
		if (basket) thunkAPI.dispatch(setBasket(basket));
		localStorage.setItem("user", JSON.stringify(user));
		history.push("/");
		return user;
	} catch (error: any) {
		return thunkAPI.rejectWithValue({ error: error.data });
	}
});

export const getCurrentUser = createAsyncThunk<User>(
	"account/getCurrentUser",
	async (_, thunkAPI) => {
		thunkAPI.dispatch(setUser(JSON.parse(localStorage.getItem("user")!)));
		try {
			const userDto = await service.Account.currentUser();
			const { basket, ...user } = userDto;
			if (basket) thunkAPI.dispatch(setBasket(basket));
			localStorage.setItem("user", JSON.stringify(user));
			return user;
		} catch (error: any) {
			thunkAPI.rejectWithValue({ error: error.data });
		}
	},
	{
		condition: () => {
			if (!localStorage.getItem("user")) return false;
		},
	}
);

export const accountSlice = createSlice({
	name: "account",
	initialState,
	reducers: {
		signOut: (state) => {
			state.user = null;
			localStorage.removeItem("user");
			history.push("/login");
		},
		setUser: (state, action) => {
			state.user = action.payload;
		},
	},
	extraReducers: (builder) => {
		builder.addCase(getCurrentUser.rejected, (state) => {
			state.user = null;
			localStorage.removeItem("user");
			toast.error("Session expired, please login again");
			history.push("/");
		});
		builder.addMatcher(isAnyOf(signInUser.fulfilled, getCurrentUser.fulfilled), (state, action) => {
			state.user = action.payload;
		});
		builder.addMatcher(isAnyOf(signInUser.rejected), (state, action) => {
			throw action.payload;
		});
	},
});

export const { signOut, setUser } = accountSlice.actions;
